import DdzBaseHandler from "./DdzBaseHandler";

export default class DdzReadyHandler extends DdzBaseHandler{
    protected execute(msg){
        this.context.isWaitMatch = false;
        this.mainUI.matchPlayerView.active = false;
        let time = msg._timeo *1000 - (Date.now() - msg._receiveTime);
        // if (time <= 0)
        //     time = 1000;
        if (time >= 1000)
            this.mainUI.showActionTimer(true, 0, time, null);
    }

    protected executeSync(msg){
        this.execute(msg);
    }
}